<template>
  <div class="StudentInfos">
    <el-card>

      <el-card class="main-card">

        <el-form
            :inline="true"
            class="demo-form-inline"
            size="small"
            :model="formInline">
          <el-form-item class="el-btn-add">
            <el-button type="primary"   @click="addStudent">新增</el-button>
          </el-form-item>
          <div class="el-btn-cha">
            <el-form-item>
              <el-button type="primary" @click="find">查询</el-button>
            </el-form-item>
            <el-form-item label="姓名">
              <el-input v-model="formInline.name"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="reset">重置</el-button>
            </el-form-item>
          </div>
        </el-form>

        <el-main class="main-size">
          <el-table :data="compData" border style="width: 100%">
            <el-table-column prop="name" label="姓名" align="center"></el-table-column>
            <el-table-column prop="sex_text" label="性别" align="center"></el-table-column>
            <el-table-column prop="age" label="年龄" align="center"></el-table-column>
            <el-table-column prop="number" label="学号" align="center"></el-table-column>
            <el-table-column prop="class" label="班级" align="center"></el-table-column>
            <el-table-column prop="state_text" label="状态" align="center"></el-table-column>
            <el-table-column prop="address" label="地址" align="center"></el-table-column>
            <el-table-column prop="phone" label="联系方式" align="center"></el-table-column>
            <el-table-column label="操作" align="center">
              <template slot-scope="scope">

                <el-button type="danger" size="mini" icon="el-icon-edit" @click="edit(scope.row)"></el-button>
                <el-button type="danger" size="mini" icon="el-icon-delete" @click="del(scope.row)"></el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-main>
      </el-card>

      <el-footer class="footer-card">
        <el-card class="card">
          <el-pagination
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :current-page="currentPage"
              :page-sizes="[5, 10, 20, 30,50]"
              :page-size="pageSize"
              layout="total, sizes, prev, pager, next, jumper"
              :total="total">
          </el-pagination>
        </el-card>
      </el-footer>
    </el-card>

    <el-dialog
        :title="state? '添加学生信息':'修改学生信息'"
        :visible.sync="dialogFormVisible"
        width="500px"
    >

      <el-form :model="form" :rules="rules" ref="form" class="el-form-box">

        <el-form-item label="姓名" :label-width="formLabelWidth" prop="name">
          <el-input v-model="form.name" auto-complete="off"></el-input>
        </el-form-item>

        <el-form-item label="性别" :label-width="formLabelWidth" prop="sex">
          <el-radio v-model="form.sex_text" label="男">男</el-radio>
          <el-radio v-model="form.sex_text" label="女">女</el-radio>
        </el-form-item>

        <el-form-item label="年龄"   :label-width="formLabelWidth" prop="age">
          <el-input v-model="form.age" auto-complete="off"></el-input>
        </el-form-item>

        <el-form-item label="学号"  :label-width="formLabelWidth" prop="number">
          <el-input v-model="form.number" auto-complete="off"></el-input>
        </el-form-item>

        <el-form-item label="班级"  :label-width="formLabelWidth" prop="class">
          <el-input v-model="form.class" auto-complete="off"></el-input>
        </el-form-item>

        <el-form-item label="状态"  :label-width="formLabelWidth" prop="state_text">
          <el-select v-model="form.state_text" placeholder="请选择">
            <el-option label="未入学" value="未入学">未入学</el-option>
            <el-option label="休学中" value="休学中">休学中</el-option>
            <el-option label="已入学" value="已入学">已入学</el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="地址"  :label-width="formLabelWidth" prop="address">
          <el-input v-model="form.address" auto-complete="off"></el-input>
        </el-form-item>

        <el-form-item label="入校时间" :label-width="formLabelWidth" prop="time">
          <el-date-picker v-model="form.time" format="yyyy 年 MM 月 dd 日" value-format="yyyy-MM-dd" type="date" placeholder="选择日期">
          </el-date-picker>
        </el-form-item>

        <el-form-item label="联系方式" :label-width="formLabelWidth" prop="phone">
          <el-input v-model="form.phone" auto-complete="off"></el-input>
        </el-form-item>

      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button @click="closeInfo('form')">取 消</el-button>
        <el-button type="primary" @click="sure('form')">确 定</el-button>
      </div>

    </el-dialog>

  </div>
</template>

<script>
import StudentsData from '@/assets/Json/StudentsData.json'
export default {
  name: "Student",
  components:{
  },
  data() {
    return {
      tableData:'',
      currentPage:1,  //当前页数
      pageSize:10,    //每一页显示条数
      total:StudentsData.length,        //总条数
      dialogFormVisible:false,
      form:{
        name:'',
        sex:'1',
        sex_text:'',
        age:'',
        number:'',
        class:'',
        state_text:'',
        address:'',
        time:'',
        phone:'',
      },
      formLabelWidth:"80px",
      rules:{
        name:[{required:true,message:"请输入姓名"}],
        sex:[{required:true}],
        age:[{required:true,message:"请输入年龄"}],
        address:[{required:true,message:"请输入地址"}],
        state_text:[{required:true,message:"请输入状态"}],
        class:[{required:true,message:"请输入班级"}],
        number:[{required:true,message:"请输入学号"}],
        time:[{required:false,message:"请输入入学时间"}],
        phone:[{required:true,message:"请输入联系方式"}],
      },
      state:true,
      formInline:{
        name:'',
      }
    }
  },
  computed:{
    compData(){
      return  this.tableData.slice((this.currentPage-1)*this.pageSize,this.currentPage*this.pageSize)
    },
  },
  created() {
    this.getData(this.formInline.name)
  },
  methods: {
    closeInfo(form){
      this.dialogFormVisible = false
      this.$refs[form].resetFields()
    },
    del(row) {
      console.log(row)
      this.$message({message:'没有操作权限',type:'warning'})
    },
    edit(row) {
      console.log(row)
      this.dialogFormVisible=true;
      this.state=false;
      this.form={...row};
    },
    addStudent(){
      this.form={
        name:'',
        sex:'',
        age:'',
        father:'',
        mather:'',
        address:'',
        time:'',
        phone:'',
      },
          this.state=true;
          this.dialogFormVisible=true;
    },
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
      this.pageSize = val
      this.currentPage = 1
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      this.currentPage = val
    },
    sure(form){
        this.$refs[form].validate(res=>{
          if(res){
            if(this.state){
              this.$message({message:'没有添加用户权限',type:'warning'})
            }else{
              this.$message({message:'没有修改用户权限',type:'warning'})
            }
          }
        })
    },
    getData(params){
          this.tableData=StudentsData.filter(function(item){
            if(params===""){
              return item
            }else {
              return item.name===params
            }
      })
    },
    find(){
      this.getData(this.formInline.name)
    },
    reset(){
      this.formInline.name=""
      this.getData(this.formInline.name)
    },
  },
}
</script>

<style scoped lang="scss">
.StudentInfos {
  width: 100%;
  height: 95%;
  margin: 10px;
  .el-card {
    width: 99%;
    height: 100%;
    .el-main{
      height:370px;
      padding-top: 0;
    }
  }
}
.main-card{
  height: 100%;
  margin: 10px;
  .main-size{
    height: 100%;
  }
  .el-form{
    .el-btn-add {
      text-align: left;
      float: left;
    }
    .el-btn-cha{
      text-align: center;
    }
  }
}
.footer-card{
  padding: 0 5px 0 10px;
  .card{
    width: 100%;
  }
}
.StudentInfo .el-card .el-main[data-v-e1b761d6] {
  height: 420px;
}
.el-form-box{
  text-align:left;
}
</style>